<script>
import VueFaqAccordion from "vue-faq-accordion";
import moment from "moment";

import ArticleInstance from "@/components/ArticleInstance.vue";

export default {
  name: "Calendar",
  components: {
    ArticleInstance,
    VueFaqAccordion,
  },
  computed: {
    calendarPage() {
      return this.$store.state.homePageData?.calendarPage;
    },
    calendarItems() {
      return this.calendarPage?.map((event) => ({
        ...event,
        title: `${
          event?.eventDate ? moment(String(event.eventDate)).format("DD MMMM YYYY") : ""
        }  |  ${event?.eventTitle ?? ""}`,
      }));
    },
  },
  data() {
    return {};
  },
  methods: {
    moment,
  },
};
</script>

<template>
  <ArticleInstance class="calendar-page" title="Calendar" @close="$emit('close')">
    <VueFaqAccordion
      v-if="calendarItems"
      class="vue-accordion"
      activeColor="#ffffff"
      borderColor="#ffffff"
      fontColor="#9e9e9e"
      :items="calendarItems"
      v-slot="itemData"
    >
      <img class="image" :src="itemData.eventPhoto.url" />
      <div class="time" v-if="itemData.eventDate">
        <time class="block">{{ moment(String(itemData.eventDate)).format("DD") }}</time>
        <time class="block">{{ moment(String(itemData.eventDate)).format("MMMM") }}</time>
        <time class="block">{{ moment(String(itemData.eventDate)).format("hh:mm") }}</time>
        <time class="block">{{ moment(String(itemData.eventDate)).format("YYYY") }}</time>
      </div>

      <div class="second-container">
        <h4>{{ itemData.eventDescription }}</h4>
        <p>{{ itemData.eventLocation }}</p>
      </div>
    </VueFaqAccordion>
  </ArticleInstance>
</template>

