<script>
import { mapMutations } from "vuex";
import { request } from "@/datocms";
import "@fortawesome/fontawesome-free/css/all.css";
import "@fortawesome/fontawesome-free/js/all.js";

import Home from "./components/Home.vue";

const HOMEPAGE_QUERY = `query MyQuery {
  allRoots {
    _allBioPageHeading1Locales {
      locale
      value
    }
    _allBioPageHeading2Locales {
      locale
      value
    }
    _allBioPageTextLocales {
      locale
      value
    }
    _allHomePageHeading1Locales {
      locale
      value
    }
    _allHomePageHeading2Locales {
      locale
      value
    }
    _allHomePageTypedtextLocales {
      locale
      value
    }
    _allHomePageTextLocales {
      locale
      value
    }
    _allContactsPageLocales {
      locale
      value
    }
    repertoire
    photosPage {
      url
    }
    newsPage {
      image {
        url
      }
      title
      description
      date
      link
    }
    videosPage {
      videoLink {
        url
        thumbnailUrl
      }
      videoTitle
    }
    calendarPage {
      eventTitle
      eventLocation
      eventPhoto {
        url
      }
      eventDate
      eventDescription
    }
    contactsPageCard {
      contactCardAddress
      contactCardLogo {
        url
      }
      contactCardEmail
      contactCardLink
      contactCardPhone
      contactCardTitle
    }
  }
}`;

export default {
  name: "App",
  components: {
    Home,
  },
  data() {
    return {
      data: null,
      error: null,
      loading: true,
    };
  },
  async mounted() {
    try {
      this.data = await request({
        query: HOMEPAGE_QUERY,
        variables: {
          limit: 10,
        },
      });
    } catch (e) {
      this.error = e;
    } finally {
      const responseData = this.data && this.data.allRoots && this.data.allRoots[0];
      if (responseData) this.setHomePageData(responseData);
    }
    this.loading = false;
  },
  methods: {
    ...mapMutations([
      // mapMutations также поддерживает нагрузку:
      "setHomePageData", // `this.setHomePageData(amount)` будет вызывать `this.$store.commit('setHomePageData', amount)`
    ]),
  },
};
</script>

<template>
  <div id="app">
    <Home />
    <!--    <router-view/>-->
  </div>
</template>

