<script>
import ClickOutside from "vue-click-outside";
import { VueTypedJs } from "vue-typed-js";

import Bio from "./Bio.vue";
import Calendar from "./Calendar.vue";
import Contacts from "./Contacts.vue";
import News from "./News.vue";
import LanguageButton from "./LanguageButton.vue";
import Photos from "./Photos.vue";
import Videos from "./Videos.vue";
import Repertoire from "./Repertoire.vue";
import { mapMutations } from "vuex";

export default {
  name: "Home",
  components: {
    Bio,
    Calendar,
    Contacts,
    News,
    Photos,
    LanguageButton,
    Videos,
    Repertoire,
    VueTypedJs,
  },
  data() {
    return {
      languages: [
        { name: "En", isSelected: true },
        { name: "Ru", isSelected: false },
      ],
      showedArticle: undefined,
    };
  },
  computed: {
    heading1() {
      return this.homePageData?._allHomePageHeading1Locales[this.selectedLanguageIndex]?.value;
    },
    heading2() {
      return this.homePageData?._allHomePageHeading2Locales[this.selectedLanguageIndex]?.value;
    },
    text() {
      return this.homePageData?._allHomePageTextLocales[this.selectedLanguageIndex]?.value;
    },
    typedText() {
      return this.homePageData?._allHomePageTypedtextLocales[this.selectedLanguageIndex]?.value;
    },
    homePageData() {
      return this.$store.state.homePageData;
    },
    selectedLanguage() {
      return this.languages.find((language) => language.isSelected).name;
    },
    selectedLanguageIndex() {
      return this.$store.state.selectedLanguageIndex;
    },
  },
  watch: {
    typedText: function (val) {
      const typedTextElement = this.$refs.typedText;
      if (typedTextElement) {
        typedTextElement.typedObj.strings = val;
        typedTextElement.typedObj.reset();
      }
    },
  },
  methods: {
    ...mapMutations([
      // mapMutations также поддерживает нагрузку:
      "setSelectedLanguageIndex", // `this.setHomePageData(amount)` будет вызывать `this.$store.commit('setHomePageData', amount)`
    ]),
    closeArticle() {
      document.body.classList.remove("is-article-visible");
      this.showedArticle = undefined;
      //this[`is${article}Show`] = false;
    },
    openArticle(article) {
      document.body.classList.add("is-article-visible");
      this.showedArticle = article;
      //this[`is${article}Show`] = true;
    },
    onSelectLanguage(selectedName) {
      this.languages.map((language) =>
        language.name === selectedName
          ? (language.isSelected = true)
          : (language.isSelected = false)
      );
      this.setSelectedLanguageIndex(this.selectedLanguage === "En" ? 0 : 1);
    },
  },
  directives: {
    ClickOutside,
  },
};
</script>
<template>
  <div class="is-preload">
    <!-- Wrapper -->
    <div id="wrapper" class="home-page">
      <LanguageButton :languages="languages" @select-language="onSelectLanguage" />
      <!-- Header -->
      <header v-if="!showedArticle" id="header" class="home-page_header">
        <div class="logo">
          <!--							<span class="icon fa-gem"></span>-->
          <img src="@/assets/images/logo_white.svg" alt="logo" />
        </div>
        <div class="content">
          <div class="inner">
            <h1>{{ heading1 }}</h1>
            <h3>{{ heading2 }}</h3>
            <template v-if="typedText">
              <VueTypedJs :strings="typedText" :loop="true" :showCursor="false" ref="typedText">
                <p class="typing"></p>
              </VueTypedJs>
            </template>
          </div>
        </div>
        <nav>
          <ul class="first-row">
            <li>
              <a @click="openArticle('Bio')">{{ ["Bio", "Био"][selectedLanguageIndex] }}</a>
            </li>
            <li>
              <a @click="openArticle('News')">{{ ["News", "Новости"][selectedLanguageIndex] }}</a>
            </li>
            <li>
              <a @click="openArticle('Photos')">{{ ["Photos", "Фото"][selectedLanguageIndex] }}</a>
            </li>
            <li>
              <a @click="openArticle('Videos')">{{ ["Videos", "Видео"][selectedLanguageIndex] }}</a>
            </li>
            <!--<li><a href="#elements">Elements</a></li>-->
          </ul>
          <ul>
            <li>
              <a @click="openArticle('Repertoire')">{{
                ["Repertoire", "Репертуар"][selectedLanguageIndex]
              }}</a>
            </li>
            <li>
              <a @click="openArticle('Calendar')">{{
                ["Calendar", "Календарь"][selectedLanguageIndex]
              }}</a>
            </li>
            <li>
              <a @click="openArticle('Contacts')">{{
                ["Contacts", "Контакты"][selectedLanguageIndex]
              }}</a>
            </li>
          </ul>
        </nav>
      </header>

      <!-- Main -->
      <transition name="fade">
        <div id="main" v-if="showedArticle">
          <!--          v-click-outside="closeArticle"-->
          <keep-alive>
            <component :is="showedArticle" @close="closeArticle"></component>
          </keep-alive>
        </div>
      </transition>

      <!-- Footer -->
      <footer id="footer">
        <p class="copyright">&copy; Copyright: Anton Glebov. 2020</p>
      </footer>
    </div>

    <!-- BG -->
    <div id="bg"></div>
  </div>
</template>

<style lang="scss" scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}

.home-page {
  &_header {
    .logo {
      img {
        height: 80%;
        width: 80%;
      }
    }
    nav {
      ul {
        justify-content: space-around;
        &:first-of-type {
          li {
            @media screen and (min-width: 481px) {
              width: 25%;
            }
          }
        }
        &:nth-of-type(2) {
          li {
            @media screen and (min-width: 481px) {
              width: 50%;
            }
          }
        }
      }
    }
    .content {
      width: 70vw;
      .inner {
        .typed-element {
          display: block;
          height: 90px;
          overflow: hidden;
          .typing {
            margin: 0;
          }
        }
      }
    }
  }
}
</style>
