<script>
import VueFaqAccordion from "vue-faq-accordion";
import moment from "moment";

import ArticleInstance from "@/components/ArticleInstance.vue";

export default {
  name: "News",
  components: {
    ArticleInstance,
    VueFaqAccordion,
  },
  computed: {
    homePageData() {
      return this.$store.state.homePageData;
    },
    news() {
      return this.homePageData?.newsPage;
    },
  },
  mounted() {
    if (this.$refs.VueFaqAccordion) {
      this.$refs.VueFaqAccordion.activeQuestionIndex = 0;
    }
  },
  methods: {
    moment,
  },
};
</script>

<template>
  <ArticleInstance title="News" @close="$emit('close')">
    <VueFaqAccordion
      v-if="news"
      ref="VueFaqAccordion"
      class="vue-accordion"
      activeColor="#ffffff"
      borderColor="#ffffff"
      fontColor="#9e9e9e"
      :initialQuestionIndex="0"
      :items="news"
    >
      <template v-slot="itemData">
        <a :href="(itemData && itemData.link) || '#'"
          ><img class="image" :src="itemData.image.url"
        /></a>
        <div class="time" v-if="itemData.date">
          <time class="block">{{ moment(String(itemData.date)).format("DD") }}</time>
          <time class="block">{{ moment(String(itemData.date)).format("MMMM") }}</time>
          <time class="block">{{ moment(String(itemData.date)).format("hh:mm") }}</time>
          <time class="block">{{ moment(String(itemData.date)).format("YYYY") }}</time>
        </div>

        <div class="second-container">
          <p>{{ itemData.description }}</p>
        </div>
      </template>
    </VueFaqAccordion>
  </ArticleInstance>
</template>

