<script>
import Vue from "vue";
import VueImg from "v-img";

const vueImgConfig = {
  // Consider alt of image as its title in gallery?
  altAsTitle: true,
};
Vue.use(VueImg, vueImgConfig);

import VueMasonryWall from "vue-masonry-wall";

import ArticleInstance from "@/components/ArticleInstance.vue";

export default {
  name: "Photos",
  components: {
    ArticleInstance,
    VueMasonryWall,
  },
  data: () => ({
    loadedGroup: 1,
    photosInGroup: 3,
    options: {
      width: 300,
      padding: {
        default: 12,
        1: 6,
        2: 8,
      },
    },
  }),
  computed: {
    homePageData() {
      return this.$store.state.homePageData;
    },
    photos() {
      return this.homePageData?.photosPage ?? [];
    },
    loadedPhotos() {
      const size = this.loadedGroup * this.photosInGroup;
      return this.photos.slice(0, size);
    },
    photoUrls() {
      const rawPhotos = this.homePageData?.photosPage ?? [];
      return rawPhotos.map((photo) => photo.url);
    },
  },
  methods: {
    append() {
      this.loadedGroup++;
    },
  },
};
</script>

<template>
  <ArticleInstance title="Photos" @close="$emit('close')" class="photos">
    <vue-masonry-wall :items="loadedPhotos" :options="options" @append="append">
      <template v-slot:default="{ item }">
        <div class="Item">
          <img v-img:group :src="item.url" />
        </div>
      </template>
    </vue-masonry-wall>
  </ArticleInstance>
</template>

