<script>
import ArticleInstance from "@/components/ArticleInstance.vue";

export default {
  name: "Bio",
  components: {
    ArticleInstance,
  },
  computed: {
    homePageData() {
      return this.$store.state.homePageData;
    },
    heading1() {
      return this.homePageData?._allBioPageHeading1Locales[this.selectedLanguageIndex]?.value;
    },
    heading2() {
      return (
        this.homePageData?._allBioPageHeading2Locales[this.selectedLanguageIndex]?.value
      );
    },
    text() {
      return (
        this.homePageData?._allBioPageTextLocales[this.selectedLanguageIndex]?.value
      );
    },
    selectedLanguageIndex() {
      return this.$store.state.selectedLanguageIndex;
    },
  },
};
</script>

<template>
  <ArticleInstance title="Bio" @close="$emit('close')">
    <h3>{{ heading1 }}</h3>
    <h5>{{ heading2 }}</h5>
    <span class="image main"><img src="@/assets/images/bg.jpg" alt="" /> </span>
    <p class="text">
      {{ text }}
    </p>
  </ArticleInstance>
</template>

<style scoped>
.text {
  white-space: pre-wrap;
}
</style>
