<script>
//install via npm
require("videojs-youtube"); // add any plugin first and then

import "video.js/dist/video-js.css";

import { videoPlayer } from "vue-video-player";

import ArticleInstance from "@/components/ArticleInstance.vue";

export default {
  name: "Videos",
  components: {
    ArticleInstance,
    videoPlayer,
  },
  data() {
    return {
      defaultPlayerOptions: {
        language: "en",
        fluid: true,
        techOrder: ["youtube"],
      },
    };
  },
  computed: {
    homePageData() {
      return this.$store.state.homePageData;
    },
    videos() {
      return this.homePageData?.videosPage;
    },
  },
};
</script>

<template>
  <ArticleInstance title="Video" @close="$emit('close')">
    <section>
      <div class="videos-wrapper">
        <div v-for="video in videos" class="video-wrapper">
          <video-player
            class="video-player-box"
            ref="videoPlayer"
            :options="{
              ...defaultPlayerOptions,
              sources: [
                {
                  type: 'video/youtube',
                  src: video.videoLink.url,
                },
              ],
              poster: video.videoLink.thumbnailUrl,
            }"
            :playsinline="true"
          >
          </video-player>
          <hr />
          <p v-if="video && video.videoTitle" class="description">
            {{ video.videoTitle }}
          </p>
        </div>
      </div>
    </section>
  </ArticleInstance>
</template>

